<template>
  <mobile-tear-sheet>
    <mu-list>
      <mu-list-item disabled title="" describeText=""/>
    </mu-list>
    <mu-divider />
    <mu-list>
      <mu-sub-header></mu-sub-header>
      <mu-list-item disabledRipple @click="handleToggle('events')" title="">
        <mu-switch v-model="events"  slot="right"/>
      </mu-list-item>
      <mu-list-item disabledRipple @click="handleToggle('calls')" title="">
        <mu-switch v-model="calls" slot="right"/>
      </mu-list-item>
      <mu-list-item disabledRipple @click="handleToggle('messages')" title="">
        <mu-switch v-model="messages" slot="right"/>
      </mu-list-item>
    </mu-list>
    <mu-list>
      <mu-sub-header></mu-sub-header>
      <mu-list-item disabledRipple @click="handleToggle('notifications')"  title="">
        <mu-checkbox v-model="notifications" slot="left"/>
      </mu-list-item>
      <mu-list-item disabledRipple @click="handleToggle('sounds')"  title="">
        <mu-checkbox v-model="sounds" slot="left"/>
      </mu-list-item>
      <mu-list-item disabledRipple @click="handleToggle('videoSounds')" title="">
        <mu-checkbox v-model="videoSounds" slot="left"/>
      </mu-list-item>
    </mu-list>
  </mobile-tear-sheet>
</template>

<script>
import mobileTearSheet from '../../../components/mobileTearSheet'
export default {
  data () {
    return {
      events: false,
      calls: false,
      messages: false,
      notifications: false,
      sounds: false,
      videoSounds: false
    }
  },
  methods: {
    handleToggle (key) {
      console.log('toggle')
      this[key] = !this[key]
    }
  },
  components: {
    'mobile-tear-sheet': mobileTearSheet
  }
}
</script>



